





.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1);
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal {
  width: 240px;
  background-color: #fff;
  padding: 48px 20px 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: scroll;
  transform: translate(-50%, -50%);
}

.modal__close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close-btn:hover, .modal__close-btn:focus {
  fill: #ff6b01;
}

.modal__close-icon {
  width: 30px;
  height: 30px;
}

.modal__poster-img {
  width: 240px;
  display: block;
}

.modal__top-text {
  margin-top: 20px;
  margin-bottom: 20px;
}

.modal__table {
  margin-bottom: 20px;
}

.modal__table-item:not(:last-child) {
  margin-bottom: 8px;
}

.modal__table-top {
  color: #8c8c8c;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.modal__table-text--header {
  margin-bottom: 8px;
}

.modal__table-text {
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.vote {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #ff6b01;
  border-radius: 5px;
  align-items: center;
  padding: 1px 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

.modal__text--header {
  text-transform: uppercase;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.modal__text {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
}

.modal__btn-list {
  margin-top: 20px;
  display: flex;
}

.modal__btn-item:not(:last-child) {
  margin-right: 20px;
}

.modal__choice-btn {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #000;
  border-radius: 5px;
  align-items: center;
  padding: 6px 27px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition-property: background, color, border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.modal__choice-btn:hover, .modal__choice-btn:focus {
  color: #fff;
  background: #ff6b01;
  border: none;
}

@media screen and (mix-width: 321px) and (max-width: 480px) {
  .modal {
    width: 440px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 602px;
    padding: 40px 72px 40px 36px;
  }

  .modal__info {
    display: flex;
  }

  .modal__poster-img {
    width: 264px;
    margin-right: 68px;
  }

  .modal__top-text {
    margin-top: 0;
    font-size: 20px;
    line-height: 1.15;
  }

  .modal__table-text {
    line-height: 1.16;
  }

  .modal__btn-item:not(:last-child) {
    margin-right: 15px;
  }

  .modal__choice-btn {
    padding: 14px 8px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 782px;
    padding: 40px 12px;
  }

  .modal__poster-img {
    width: 375px;
    margin-right: 16px;
  }

  .modal__top-text {
    font-size: 30px;
    line-height: 1.17;
  }
}



